@media screen and (min-width: 0px) and (max-width: 599px) {
  .contact-greeting {
    margin: 10rem 0 0 5rem;
    font-size: 4rem;
    font-weight: 800;
  }
  .contact-message {
    font-size: 1rem;
    margin: 0rem 0 0.25rem 5rem;
  }
  .contact {
    font-size: 1rem;
    margin: 0rem 0 0 5rem;
    .contact__message {
      margin-left: 0.8rem;
    }
  }
}
@media screen and (min-width: 600px) and (max-width: 768px) {
  .contact-greeting {
    margin: 10rem 0 0 5rem;
    font-size: 6rem;
    font-weight: 800;
  }
  .contact-message {
    font-size: 1.5rem;
    margin: 0rem 0 0.25rem 5rem;
  }
  .contact {
    font-size: 1.5rem;
    margin: 0rem 0 0 5rem;
    .contact__message {
      margin-left: 0.8rem;
    }
  }
}

@media screen and (min-width: 769px) and (max-width: 1280px) {
  .contact-greeting {
    margin: 10rem 0 0 10rem;
    font-size: 8rem;
    font-weight: 800;
  }
  .contact-message {
    font-size: 1.5rem;
    margin: 0rem 0 0.25rem 10rem;
  }
  .contact {
    font-size: 1.5rem;
    margin: 0rem 0 0 10rem;
    .contact__message {
      margin-left: 0.8rem;
    }
  }
}
